@import url(https://fonts.googleapis.com/css2?family=Oswald&family=Righteous&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    text-align: center;
    font-size: 20px;
    background-color: #4e6881;
    color: white;
}

h1,h2,h3,h4,h5{
    font-family: 'Oswald';
}

p{
    font-family: 'Righteous';
}
.profile img{
    height: 150px;
    width: 150px;
    border: none;
    border-radius: 50%;
    margin-top: 1.5rem;
}

a {
    color: white;
    text-decoration: none;
    font-family: monospace;
}
.btn{
    color: white;
    width:25%;
    background: transparent;
    outline: none;
    border: 2px solid white;
    padding: 8px 15px;
    border-radius: 10px;
    font-family: Tahoma;
}

.btn:hover{
    background-color: #808B96;
    color: black;
}

#icon{
    width: 38px;
}

#icon:hover{
    width: 35px;
}

#box2{
    background-color: #808B96;
}
#box3{
    margin-top: 2rem;
    background-color: #808B96;
}

.title-fade-in{
    opacity: 100;
    -webkit-animation: fade-in 2s;
            animation: fade-in 2s;
}

.title-fade-out{
    opacity: 0;
    -webkit-animation: fade-out 2s;
            animation: fade-out 2s;
}

@-webkit-keyframes fade-in{
    from{opacity:0}
    to{opacity: 100}
}

@keyframes fade-in{
    from{opacity:0}
    to{opacity: 100}
}

@-webkit-keyframes fade-out{
    from{opacity:100}
    to{opacity: 0}
}

@keyframes fade-out{
    from{opacity:100}
    to{opacity: 0}
}
